.map {
  min-height: 400px;
  background-image: url("../img/Map.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  display: flex;
  align-items: center;
}

.map__info {
  background-color: #fff;
  padding: 60px 78px 60px 80px;
  width: 407px;
  display: flex;
  gap: 87px;
  align-items: center;
}

.map__title {
  font-size: 20px;
  font-weight: 400;
  line-height: 26px;
  color: #111;
  text-transform: uppercase;
  margin: 0;
}

.map__address {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #444;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 19px;

  & p {
    margin: 0;
  }
}
